.shoptitle{
    display:flex;
    margin-top:50px;
    justify-content: center;
    align-items: center;
    font-size: 50px;
}

.product{
    width:100%;
    height:auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
}

.item{
    border-radius:15px;
    width:300px;
    height: 350px;
    margin:100px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.item img{
    width:400px;
}

.item .description{
    text-align: center;
}

.item:hover{
    transition: 0.3s ease-in;
}



